<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="所属角色" :label-width="formLabelWidth">
          <el-select v-model="form.roleid" placeholder="请选择角色">
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.rolename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" :label-width="formLabelWidth">
          <el-input
            type="password"
            v-model="form.password"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="#409eff"
            inactive-color="#dcdfe3"
            :active-value=1
            :inactive-value=2
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button @click="ok"  v-if="info.isaAdd" type="primary">添 加</el-button>
        <el-button @click="editM" v-else type="primary">修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { addManger,mangerOne,editManger } from "../../util/request";
import { successAlert, warningAlert } from "../../util/alert";
export default {
  props: ["info"],
  data() {
    return {
      form: {
        roleid: "",
        username: "",
        password: "",
        status: 1,
      },
      formLabelWidth: "120px",
    };
  },
  computed: {
    ...mapGetters({
      roleList: "role/getRolelist",
    }),
  },
  methods: {
    ...mapActions({
      getRoleList: "role/roleActions",
      getMangelist:"manger/mangerActions",
      getCount:"manger/countActions"
    }),
    cancel() {
      this.info.isShow = false;
      this.form = {
        roleid: "",
        username: "",
        password: "",
        status: 1,
      };
    },
    ok(){
        addManger(this.form).then(res=>{
            if(res.data.code==200){
                successAlert(res.data.msg)
                this.cancel()
                this.getMangelist()
                this.getCount();
            }else{
                warningAlert(res.data.msg)
            }
        })
    },
    edit(id){
        mangerOne({uid:id}).then(res=>{
            this.form=res.data.list
            this.form.password=""
        })
    },
    editM(){
        editManger(this.form).then(res=>{
            if(res.data.code==200){
                successAlert(res.data.msg)
                this.cancel()
                this.getMangelist();
            }else{
                warningAlert(res.data.msg)
            }
        })
    },

  },
  mounted() {
    this.getRoleList();
    this.getMangelist();
    //添加成功后条数也发生改变
    this.getCount();
  },
};
</script>

<style>
</style>


