<template>
  <div>
    <el-table
      :data="info"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
    >
      <el-table-column prop="id" label="用户编号" width="180">
      </el-table-column>
      <el-table-column
        prop="username"
        label="用户名"
        width="180"
      ></el-table-column>
      <el-table-column prop="rolename" label="所属角色"> </el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1" @click="scope.row.status=2">启用</el-button>
          <el-button type="danger" v-else @click="scope.row.status=1">禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="id" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.uid)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.uid)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="size"
      :total="count"
      @current-change="cPage"
    ></el-pagination>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { successAlert } from '../../util/alert';
import {delManger} from "../../util/request"
export default {
  data() {
    return {
      data: [],
    };
  },
  computed: {
    ...mapGetters({
      info: "manger/getManger",
      count: "manger/getCount",
      size: "manger/getSize",
    }),
  },
  methods: {
    ...mapActions({
      mangerList: "manger/mangerActions",
      getCount: "manger/countActions",
      getPage: "manger/pageActions",
    }),
    //获取当前页码数
    cPage(page) {
      this.getPage(page);
      this.mangerList();
    },
    edit(id){
        this.$emit("editManger",id)
    },
    del(id){
       this.$confirm("确定要删除吗?","提示",{
         confirmButtonText:"确定",
         cancelButtonText:"取消",
         type: "warning",
       }).then(()=>{
         delManger({uid:id}).then(res=>{
           if(res.data.code==200){
             successAlert(res.data.msg)
             this.mangerList();
             this.getCount();
           }
         })
       }).catch(()=>{
         this.$message({
           type:"info",
           message:"已取消删除"
         })
       })
      
    }
  },
  mounted() {
    this.mangerList();
    this.getCount();
  },
};
</script>

<style>
</style>


