<template>
  <div>
     <el-button type="primary" @click="add" >添加</el-button>
     <v-add :info="info" ref="add"></v-add>
     <v-list @editManger="editManger($event)" ></v-list>
  </div>
</template>

<script>
import vAdd from "./add"
import vList from "./list"
export default {
  data(){
    return {
      info:{
        isShow:false,
        title:"",
        isaAdd:true
      }
    }
  },
  methods:{
    add(){
      this.info.isShow=true
      this.info.isaAdd=true
      this.info.title="添加管理员"
    },
    editManger(id){
      this.info.isShow=true
      this.info.isaAdd=false
      this.info.title="编辑管理员"
      this.$refs.add.edit(id)
    },
  },
  components:{
    vAdd,
    vList
  }
}
</script>

<style scoped>
.el-button{
  margin: 20px 0;
}
</style>


